.Slider{width:100%;height:560px;}
.about_us{width:50%;height:100px;border:4px solid black;border-top: none;border-right:none;border-bottom:none;}
.about_title p{font-size: 19px;color:grey;}
.about_title{width:55%;}
.border_about{border: 4px dotted yellow;height:270px;width:100%;}


div.gallery {
    border: 1px solid #ccc;
    height:auto;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  }
  
  div.gallery:hover {
    border: 1px solid #777;
  }
  
  div.gallery img {
    width: 100%;
    height: auto;
  }
  
  div.desc {
    padding: 5px;
  }
    
  .desc p{text-align: center;}
  
.desc_texts{float:left;}.desc_texts1{float:right;}
  
  @media only screen and (max-width: 700px) {
    .responsive {
      width: 49.99999%;
      margin: 6px 0;
    }
  }
  
  @media only screen and (max-width: 500px) {
    .responsive {
      width: 100%;
    }
  }
  ::placeholder{color: black !important;}
  .button_form{background-color: black;width:160px;height:50px;border:none;}
  .clearfix:after {
    content: "";
    display: table;
    clear: both;
  }
  .input_group_icon{background-color: transparent;border:none;border-radius: none;}

  .from_structure{display: flex !important;;}
  .input_group{border:3px solid black;border-top: none;border-left: none;border-right:none;}
  .from_background{width:100%;height:200px;background-color: #f9e909df;border:1px solid yellow;}
  .banner{width:100%;height:500px;margin:0px;padding: 0px;background-attachment: fixed;}
  .play_icon{font-size: 60px;color:yellow;position:relative;display: flex;text-align: center;justify-content: center;top:-240px;}


.grid_image{width:100%;height:300px;}
    @media only screen and (min-width: 360px) and (max-width: 768px) {
    .Slider{width:100%;height:100%;}  
    .icon_list{display: none;}
    .about_us{width:50%;height:auto;border:3px solid black;border-top: none;border-right:none;border-bottom:none;}
    .from_background{width:100%;height:auto;background-color: #f9e909df;}

}